.social-networks {
    padding-top: 2rem;
    padding-bottom: 2rem;
    background-image: linear-gradient(90deg, #FF9A8B 0%, #FF6A88 55%, #FF99AC 100%);
}

 .title span {
    font-size: .8rem;
}

.social-networks .title h2 {
    font-size: 3rem;
    color: #FF9A8B;
}

.social-bloc {
    display: flex;
}

.social-networks .social-bloc .row {
    margin: 1rem;
}

.github {
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
    border: .1rem solid #FF9A8B;
    padding: 1rem;
    border-radius: .3rem;
}

.title-git a {
    color: black;
}

.linkedin {
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
    border: .1rem solid #FF9A8B;
    padding: 1rem;
    border-radius: .3rem;
}

.title-linked a {
    color: black;
}

.social-networks .social-bloc a {
    text-decoration: none;
}

.social-bloc .icon-git i, .icon-linked i {
    font-size: 3rem;
}




@media screen and (max-width: 768px) {
    .social-networks .title h2 {
        font-size: 2.5rem;
    }

    .social-bloc {
        flex-direction: column;
    }

    .github {
        text-align: center;
        flex-direction: column-reverse;
    }

    .linkedin {
        text-align: center;
        flex-direction: column-reverse;
    }
}