.recommendation {
    background-image: linear-gradient(90deg, #FF9A8B 0%, #FF6A88 55%, #FF99AC 100%);
    padding-top: 3rem;
    padding-bottom: 3rem;
}

.recommendation .title span {
    font-size: .8rem;
}

.recommendation .title h2 {
    font-size: 3rem;
    color: #FF9A8B;
}

.recommendation .testimonial {
    width: 80%;
    margin: auto;
    height: auto;
}

.testimonial-contents {
    width: 50rem;
    height: 30rem;
    position: relative;
}

.testimonial-text {
    background-color: #726e6e;
    width: 28rem;
    height: 16rem;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    color: #d3d4d6;
    border-radius: .5rem;
    padding: 3rem;
    z-index: 2;
    text-align: justify;
}

.testimonial-text h3 {
    margin-top: 1rem;
}

.recommendation .testimonial-img {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
}

.recommendation .testimonial-img img {
    border-radius: .5rem;
    height: 100%;
}






@media screen and (max-width: 768px) {
    .recommendation .title h2 {
        font-size: 2.1rem;
    }

    
    .testimonial-contents {
        width: 50rem;
        height: 25rem;
    }

    .recommendation .testimonial-img img {
        width: 50rem;
    }

    .testimonial-text {
        width: 22rem;
        height: 12rem;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        color: #d3d4d6;
        border-radius: .5rem;
        padding: 1rem;
        z-index: 2;
        text-align: justify;
    }
}