.journey {
    padding-top: 5rem;
    padding-bottom: 2rem;
    background-image: linear-gradient(90deg, #FF9A8B 0%, #FF6A88 55%, #FF99AC 100%);
}

.journey h1 {
    text-align: center;
    font-size: 3.5rem;
    color: #726e6e;
}

.experiences {
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.experiences h2 {
    font-size: 3rem;
    color: #FF9A8B;
}

.experiences-content {
    display: flex;
    flex-direction: column;
    margin: 1rem;
}

.contents {
    margin: 1rem;
}

.experiences-content h2 {
    font-size: 2rem;
}

.experiences-content p, h3, h2, strong{
    margin-bottom: 1rem;
}

.content1 {
    background-color: #FF9A8B;
    padding: 2rem;
    border-radius: 1rem;
}

.content2 {
    background-color: #FF9A8B;
    padding: 2rem;
    border-radius: 1rem;
}

.content3 {
    background-color: #FF9A8B;
    padding: 2rem;
    border-radius: 1rem;
}

.experiences-content {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
}

.experiences-content h2 {
    color: black;
}

/* Competences */

.competences {
    padding-top: 3rem;
    padding-bottom: 3rem;
}

.competences h2 {
    font-size: 3rem;
    margin-bottom: 2rem;
    color: #FF9A8B;
}

.competences-items {
    display: flex;
    color: #726e6e;
    text-align: center;
}

/* .competences-items h3 {
    text-align: center;
} */

.items {
    flex: 1;
    padding: 4rem;
    margin: 1rem;
}

.item1 {
    border: .1rem solid #FF9A8B;
}
.item1:hover {
    border: .1rem solid #F76B1C;
}

.item2 {
    border: .1rem solid #FF9A8B;
}
.item2:hover {
    border: .1rem solid #F76B1C;
}

.item3 {
    border: .1rem solid #FF9A8B;
}
.item3:hover {
    border: .1rem solid #F76B1C;
}

/* formation */

.formation {
    padding-top: 3rem;
    padding-bottom: 3rem;
}

.formation h2 {
    font-size: 3rem;
    margin-bottom: 2rem;
    color: #FF9A8B;
}

.formation-items {
    display: flex;
}

.img-formation {
    flex: 1;
}

.text-formation {
    flex: 1;
    margin-top: 2rem;
}

.text-formation p {
    color: black;
}

.text-formation .btn-formation {
    margin-top: 3rem;
}

.text-formation .btn-formation button {
    padding: .8rem;
    font-size: 1rem;
    color: #FF6A88;
    border: .1rem solid #FF6A88;
    border-radius: 2rem;
    background-color: #FF9A8B;
}



@media screen and (max-width: 768px) {
    .journey h1 {
        font-size: 2.5rem;
    }

    .experiences h2 {
        font-size: 2.5rem;
    }

    .experiences-content {
        width: 100%;
    }

    .experiences-content .contents h2 {
        font-size: 1.8rem;
    }

    .competences h2 {
        font-size: 2.5rem;
    }

    .competences-items {
        flex-direction: column;
    }

    .formation h2 {
        font-size: 2.5rem;
    }

    .formation-items {
        flex-direction: column;
    }

    .img-formation img {
        width: 16rem;
        height: 16rem;
    }
}